import React from 'react';
// import { renderRoutes } from 'react-router-config';
import { BrowserRouter as Router, Route} from 'react-router-dom';
// import routes from './routes';
import load from './load';

const LoadablePage1 = load('page1');   // 测试页面
const LoadableIndex = load('index');   // 首页


// BrowserRouter , 上线 nginx 要作相应配置  https://www.cnblogs.com/sunLemon/p/9020153.html
export default (
    <Router>
        <div className="wrap">
            <Route exact path="/" component={LoadableIndex}/>
            <Route exact path="/page1/:id" component={LoadablePage1}/>
        </div>
    </Router>
);



